.homePage {
    margin-top: 32px;
    display: flex;
    flex-direction: column;
}

.greeting {
    margin-top: 12px;
}

.logoutBtn {
    margin-top: 20px;
    width: 160px;
    height: 32px;
    display: flex;
    justify-content: center;
    align-items: center;
    font-weight: 500;
    font-size: 16px;
    line-height: 1.5;
    letter-spacing: 0.04em;
    color: #FFFFFF;
    cursor: pointer;
    background-color: #4D5AE5;
    box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.15);
    border: none;
    border-radius: 4px;
}

.logoutBtn:hover,
.logoutBtn:focus {
    background-color: brown;
}